.classify{
	// background: #FAFAFA;
	height: 45px;
	position: relative;
	&-content{
		height: 45px;
		display: flex;
		align-items: center;

		&-item{
			flex: 0 0 52px;
			width: 52px;
			text-align: center;
			color: #999;
			font-size: 16px;
			word-break: keep-all;
			&.selected{
				color: #51159D;
				font-weight: 600;
				position: relative;
				span{
					position: relative;
					z-index: 20;
				}
				&:before{
					content: '';
					position: absolute;
					width: 11px;
					height: 11px;
					background: url('../images/select.png') no-repeat;
					background-size: contain;
					background-position: center center;
					right: 3px;
					top: -3px;
					z-index: 9;
				}
			}
		}
	}
	&-icon{
		position: absolute;
		right: 0;
		top: 0;
		width: 45px;
		height: 45px;
		background: url('../images/classify.png') no-repeat;
		background-size: 25px 25px;
		background-color: #f5f5f9;
		background-position: 10px center;
		box-shadow: 0 0 15px 7px #f5f5f9
	}
}